./fonts/Roboto-Regular.ttf');\n  font-weight: 400;\n}\n\n@font-face {\n  font-family: 'Roboto';\n  src: url('../fonts/Roboto-Medium.ttf');\n  font-weight: 500;\n}\n\n@font-face {\n  font-family: 'Roboto';\n  src: url('../fonts/Roboto-Bold.ttf');\n  font-weight: 700;\n}\n\n",".header {\n    z-index: 3;\n\n    width: calc(90vw - (100vw - 100%));\n    height: 100px;\n    padding-left: 5vw;\n    padding-right: 5vw;\n\n    background-color: $BACKGROUND;\n    color: $MAIN_TEXT;\n\n    @media (max-width: 900px) {\n        position: relative;\n    }\n}\n\n.header__header-nav {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n\n    width: 100%;\n    height: 50%;\n    padding-top: 25px;\n}\n\n.logo {\n    display: flex;\n    align-items: center;\n    height: 50px;\n\n    transition: all 0.3s ease;\n\n    &:hover {\n        cursor: pointer;\n\n        .logo__image {\n            stroke: $MAIN_COLOR;\n        }\n\n        color: $MAIN_COLOR;\n\n        transition: all 0.3s ease;\n    }\n}\n\n.logo__image {\n    width: 70px;\n    height: 70px;\n    margin-right: 1vw;\n    fill: $MAIN_TEXT;\n    stroke: $MAIN_TEXT;\n\n    transition: all 0.3s ease;\n\n    @media (max-width: 400px) {\n        width: calc(25vw - 30px);\n        height: calc(25vw - 30px);\n    }\n}\n\n.logo__text {\n    font-family: 'Pulsar Original', sans-serif;\n    font-size: 50px;\n    text-transform: uppercase;\n\n    @media (max-width: 1920px) {\n        font-size: calc(1.282vw + 25.38px);\n    }\n\n    @media (max-width: 700px) {\n        display: none;\n    }\n}\n\n.widget {\n    display: flex;\n    justify-content: space-around;\n    align-items: center;\n\n    width: 100%;\n\n    & .widget__wallet {\n        width: 50px;\n        height: 50px;\n\n        fill: $MAIN_TEXT;\n\n        @media (max-width: 1000px) {\n            width: calc(3.125vw + 18.75px);\n            height: calc(3.125vw + 18.75px);\n        }\n    }\n\n    & .widget__sum {\n        margin-right: 1vw;\n        margin-left: 1vw;\n\n        font-family: 'Roboto', sans-serif;\n        font-weight: bold;\n        font-size: 30px;\n\n        @media (max-width: 1900px) {\n            font-size: calc(1.013vw + 10.76px);\n        }\n\n        &:hover {\n            cursor: default;\n        }\n    }\n\n    & .widget__cart-wrapper {\n        position: relative;\n\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        width: 80px;\n        height: 80px;\n\n        color: $MAIN_TEXT;\n\n        @media (max-width: 400px) {\n            width: calc(25vw - 20px);\n            height: calc(25vw - 20px);\n        }\n\n        &:hover {\n            cursor: pointer;\n\n            transition: all 0.3s ease;\n\n            & .widget__cart {\n                fill: $MAIN_COLOR;\n                stroke: $MAIN_COLOR;\n            }\n\n            & .sum-text {\n                color: $MAIN_COLOR;\n\n                transition: all 0.3s ease;\n            }\n        }\n\n        & .widget__cart {\n            position: absolute;\n            top: 0;\n            left: 0;\n\n            width: 100%;\n            height: 100%;\n\n            fill: $MAIN_TEXT;\n            stroke: $MAIN_TEXT;\n\n            transition: all 0.3s ease;\n        }\n\n        & .sum-text {\n            font-family: 'Aire Exterior', sans-serif;\n            font-size: 20px;\n\n            transition: all 0.3s ease;\n\n            @media (max-width: 400px) {\n                top: calc(6.25vw - 3px);\n                left: calc(10vw - 8px);\n\n                font-size: calc(7.5vw - 10px);\n            }\n        }\n    }\n}\n\n.settings {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: space-around;\n    height: 100px;\n}\n\n.lang-control {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    width: 40px;\n    height: 40px;\n    margin: 0;\n\n    text-transform: uppercase;\n    font-family: 'Pulsar Original', sans-serif;\n    font-size: 30px;\n\n    transition: all 0.3s ease;\n\n    &:hover {\n        cursor: pointer;\n        border-radius: 10px;\n        color: $MAIN_COLOR;\n\n        transition: all 0.3s ease;\n    }\n}\n\n.theme-control {\n    display: flex;\n    width: 60px;\n    height: 20px;\n    justify-content: $JUSTIFY_CONTENT;\n\n    border-radius: 10px;\n    background-image: $THEME_BG_IMAGE;\n    background-repeat: no-repeat;\n    background-size: cover;\n    outline: 1px solid $MAIN_COLOR;\n\n    transition: all 0.3s ease;\n\n    &:hover {\n        cursor: pointer;\n        border-radius: 10px;\n\n        transition: all 0.3s ease;\n    }\n\n    & .theme-control__switch {\n        width: 20px;\n        height: 20px;\n\n        border-radius: 10px;\n        background-color: $MAIN_TEXT;\n        box-shadow: 0 0 10px $MAIN_TEXT;\n\n        transition: all 0.3s ease;\n    }\n\n    &:hover .theme-control__switch {\n        background-color: $MAIN_COLOR;\n    }\n}\n","// for dark theme\n$MAIN_COLOR: #57d6ff;\n$BACKGROUND: #141c24;\n$MAIN_TEXT: #ffffff;\n$ADDITIONAL_TEXT: #ababab;\n$WHITE_TR: rgba(255, 255, 255, 0.5);\n$WHITE_INPUT: rgba(255, 255, 255, 0.1);\n$BLUE_TR: rgba(0, 197, 255, 0.5);\n$BLACK_BG: rgba(0, 0, 0, 0.1);\n$THEME_BG_IMAGE: url('../images/night.jpg');\n$JUSTIFY_CONTENT: end;\n\n// For light theme\n// $MAIN_COLOR: #0066ff;\n// $BACKGROUND: #ffffff;\n// $MAIN_TEXT: #171717;\n// $ADDITIONAL_TEXT: #616161;\n// $THEME_BG_IMAGE: url('../images/day.jpg');\n// $JUSTIFY_CONTENT: start;\n",".footer {\n  position: relative;\n  z-index: 1;\n\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n\n  width: calc(90vw - (100vw - 100%));\n  height: calc(5.102vw + 23.67px);\n  padding-left: 5vw;\n  padding-right: 5vw;\n\n  background-color: $BACKGROUND;\n  color: $MAIN_TEXT;\n\n  @media (max-width: 800px) {\n    & .logo {\n      position: absolute;\n      visibility: none;\n      display: none;\n      opacity: 0;\n    }\n\n  }\n}\n\n.year-author {\n  text-align: center;\n  font-family: 'Pulsar Original';\n  font-size: calc(1.646vw + 8.73px);\n  text-transform: uppercase;\n\n  &:hover {\n    cursor: default;\n  }\n}\n\n.github-link {\n  width: calc(3.469vw + 18.90px);\n  height: calc(3.469vw + 18.90px);\n}\n\n\n.github-link__svg {\n  fill: $MAIN_TEXT;\n\n  transition: all 0.3s ease;\n}\n\n.github-link__svg:hover {\n  fill: $MAIN_COLOR;\n\n  transition: all 0.3s ease;\n}\n\n.rsschool-link__svg {\n  width: calc(6.735vw + 32.45px);\n\n  fill: $MAIN_TEXT;\n\n  transition: all 0.3s ease;\n\n  & .svg_0,\n  & .st0 {\n    fill: none;\n  }\n\n  & .st5 {\n    fill: none;\n    stroke: $MAIN_TEXT;\n    stroke-width: 4;\n    stroke-miterlimit: 10;\n  }\n\n  & .st6,\n  & .st7 {\n    fill: none;\n    stroke: none;\n    stroke-width: 4;\n    stroke-miterlimit: 10;\n  }\n\n  & .st9 {\n    fill: none;\n    stroke: $MAIN_COLOR;\n    stroke-width: 4;\n    stroke-miterlimit: 10;\n  }\n}\n\n.rsschool-link__svg:hover {\n  fill: $MAIN_COLOR;\n\n  transition: all 0.3s ease;\n\n  & .st5,\n  & .st9 {\n    stroke: $MAIN_COLOR;\n  }\n}\n\n","\n.start {\n  position: relative;\n\n  box-sizing: border-box;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  flex-direction: column;\n\n  height: 60vw;\n  width: 100%;\n  padding-left: 5%;\n  padding-right: 5%;\n  margin-left: auto;\n  margin-right: auto;\n\n  background-color: $BACKGROUND;\n}\n\n.start__preheader {\n  position: relative;\n  z-index: 2;\n\n  width: 100%;\n  padding: 0;\n  margin: 0 auto 0 auto;\n\n  text-align: center;\n  font-family: 'Roboto', sans-serif;\n  font-weight: 500;\n  font-size: calc(0.746vw + 11.31px);\n\n  color: $MAIN_TEXT;\n  -webkit-text-stroke: 1px $BACKGROUND;\n}\n\n.start__header {\n  position: relative;\n  z-index: 2;\n\n  width: 100%;\n  padding: 0;\n  margin: 0 auto 0 auto;\n\n  text-align: center;\n  font-family: 'Aire Exterior', sans-serif;\n  font-size: calc(2.985vw + 9.25px);\n\n  color: $MAIN_TEXT;\n  -webkit-text-stroke: 1px $BACKGROUND;\n}\n\n\n.start__button {\n  position: relative;\n  z-index: 2;\n\n  display: block;\n  width: calc(11.527vw + 58.50px);\n  height: calc(2.824vw + 13.83px);\n  margin-top: calc(7.023vw - 17.28px);\n  margin-left: auto;\n  margin-right: auto;\n\n  text-align: center;\n  font-family: 'Aire Exterior', sans-serif;\n  font-size: calc(0.746vw + 11.31px);\n\n  clip-path: polygon(0 0, 90% 0%, 100% 40%, 100% 80%, 100% 100%, 20% 100%, 0 100%);\n  border: none;\n  background-repeat: no-repeat;\n  background-size: contain;\n  color: $BACKGROUND;\n  background-color: $MAIN_TEXT;\n\n  transition: transform 0.3s ease;\n  \n  &:hover {\n    cursor: pointer;\n    transform: scale(1.05);\n    transition: transform 0.3s ease;\n  }\n\n  &:active {\n    background-color: $MAIN_COLOR;\n  }\n}\n\n.start__container {\n  position: absolute;\n  z-index: 1;\n\n  width: 90vw;\n  height: 50.6vw;\n  box-sizing: border-box;\n  margin-left: auto;\n  margin-right: auto;\n\n  background-image: url(\"../images/startImage.jpg\");\n  background-size: cover;\n  background-repeat: no-repeat;\n  border-radius: 3vw;\n  outline: 1px solid $MAIN_COLOR;\n  box-shadow: 0 0 3.6vw $MAIN_COLOR;\n}\n\n.start__note {\n  position: absolute;\n  bottom: 0;\n  right: 2vw;\n  z-index: 2;\n\n  display: inline;\n  padding: 1vw;\n  border-radius: 1vw;\n  margin: 0 auto 0 auto;\n\n  text-align: center;\n  font-family: 'Roboto', sans-serif;\n  font-weight: 500;\n  font-size: 14px;\n\n  color: $MAIN_TEXT;\n  background-color: rgba(0, 0, 0, 0.372);\n\n  @media (max-width: 600px) {\n    display: none;\n  }\n}","@use 'sass:math';\n\n.store {\n    display: grid;\n    align-items: flex-start;\n    position: relative;\n    right: 50px;\n\n    width: 90vw;\n    padding-left: 5vw;\n    padding-right: 5vw;\n    margin-left: auto;\n    margin-right: auto;\n\n    background-color: $BACKGROUND;\n}\n\n.store_horizontal {\n    margin: 550px auto;\n    grid-template-columns: 1fr 3fr 3fr 3fr 1fr;\n    grid-template-rows: 50px 750px 50px 400px;\n    gap: 1vw;\n\n    grid-template-areas:\n        'sort sort search view view'\n        'arrow-left cards cards cards arrow-right'\n        'scroll scroll scroll scroll scroll'\n        'sets sets sets sets sets';\n}\n\n.store_vertical {\n    grid-template-columns: 1fr 1fr 1fr;\n    grid-template-rows: 1fr 1fr 1fr;\n    gap: 1vw;\n\n    grid-template-areas:\n        'sort search view'\n        'sets cards scroll'\n        'sets cards scroll';\n}\n\n.goods-sort {\n    grid-area: sort;\n    margin: 0;\n    width: 100%;\n\n    font-family: 'Roboto', sans-serif;\n    font-weight: 300;\n    text-align: center;\n\n    border: 3px solid $MAIN_COLOR;\n    border-radius: 5px;\n    background-color: #00000042;\n    opacity: 0.8;\n    appearance: none;\n    outline: 0;\n    box-shadow: none;\n    color: $ADDITIONAL_TEXT;\n\n    &:hover {\n        cursor: pointer;\n        opacity: 1;\n        color: #ffffff;\n    }\n\n    &::-ms-expand {\n        display: none;\n    }\n\n    & .goods-sort__item {\n        outline: none;\n        border: 3px solid $MAIN_COLOR;\n        border-radius: 5px;\n        background-color: #000000;\n    }\n}\n\n.goods-search {\n    display: flex;\n    width: 100%;\n\n    grid-area: search;\n\n    border: 3px solid $MAIN_COLOR;\n    border-radius: 5px;\n    opacity: 0.8;\n    background-color: #00000042;\n    list-style: none;\n    color: $ADDITIONAL_TEXT;\n\n    &:hover {\n        opacity: 1;\n    }\n\n    &:focus-within {\n        color: #ffffff;\n        opacity: 1;\n    }\n\n    & .goods-search__input {\n        width: 80%;\n        background-color: rgba(255, 255, 255, 0);\n        color: $MAIN_TEXT;\n        border: none;\n\n        &:focus {\n            outline: none;\n        }\n    }\n\n    & .goods-search__count {\n        margin: 0;\n        padding-top: 2px;\n        padding-bottom: 2px;\n        width: 20%;\n\n        font-family: 'Aire exterior', sans-serif;\n        text-align: center;\n\n        border-left: 3px solid $MAIN_COLOR;\n    }\n}\n\n.goods-view {\n    grid-area: view;\n    margin: 0;\n    width: 100%;\n\n    font-family: 'Roboto', sans-serif;\n    font-weight: 300;\n    text-align: center;\n\n    border: 3px solid $MAIN_COLOR;\n    border-radius: 5px;\n    background-color: #00000042;\n    opacity: 0.8;\n    appearance: none;\n    outline: 0;\n    box-shadow: none;\n    color: $ADDITIONAL_TEXT;\n\n    &:hover {\n        cursor: pointer;\n        opacity: 1;\n        color: #ffffff;\n    }\n\n    &::-ms-expand {\n        display: none;\n    }\n\n    & .goods-view__item {\n        outline: none;\n        border: 3px solid $MAIN_COLOR;\n        border-radius: 5px;\n        background-color: #000000;\n    }\n}\n\n.goods-sort,\n.goods-search,\n.goods-search__input,\n.goods-search__input::placeholder,\n.goods-view {\n    font-family: 'Roboto', sans-serif;\n    font-weight: 300;\n    font-size: calc(0.612vw + 12.04px);\n    text-align: center;\n}\n\n.arrow-left,\n.arrow-right {\n    grid-area: arrow-left;\n    align-self: center;\n    width: 4vw;\n    height: 4vw;\n\n    fill: $MAIN_TEXT;\n\n    &:hover {\n        cursor: pointer;\n        fill: $MAIN_COLOR;\n    }\n\n    &:active {\n        opacity: 0.3;\n    }\n}\n\n.arrow-right {\n    grid-area: arrow-right;\n\n    rotate: 180deg;\n}\n\n.cards-wrapper {\n    grid-area: cards;\n    overflow: hidden;\n}\n\n.cards {\n    position: relative;\n    left: 0px;\n    width: 1300px;\n    height: 750px;\n    // display: grid;\n    display: flex;\n    justify-content: space-between;\n    flex-wrap: wrap;\n\n    // grid-template-columns: 32% 32% 32% 32% 32% 32%;\n    // grid-template-columns: repeat(3, 32%);\n    grid-template-rows: 1fr 1fr;\n    gap: 1vw;\n}\n\n@for $i from 1 through 101 {\n    .cards:has(.card:nth-child(#{$i})) {\n        grid-template-columns: repeat(#{round($i / 2)}, 32%);\n    }\n}\n\n.card {\n    position: relative;\n    display: grid;\n    width: 380px;\n    height: 310px;\n    grid-template-rows: 1fr 0.5fr 2fr 0.5fr 1fr;\n    grid-template-columns: 1fr 3fr 1fr;\n    grid-template-areas:\n        'sale title rating'\n        '. subtitle .'\n        '. img .'\n        '. button .'\n        '. button .';\n    gap: 0.5vw;\n    padding: 1vw;\n    overflow: hidden;\n\n    background-color: rgba(0, 0, 0, 0.243);\n    border-radius: 1vw;\n\n    transition: all 0.3s ease;\n\n    &:hover {\n        scale: calc(1.05);\n\n        transition: all 0.3s ease;\n    }\n\n    &:hover .card__info {\n        bottom: 5px;\n\n        transition: all 0.3s ease;\n    }\n\n    &:hover .card__button {\n        margin-bottom: 20px;\n\n        transition: all 0.3s ease;\n    }\n}\n\n.card__title {\n    grid-area: title;\n    margin: 0;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n\n    font-family: 'Aire exterior', sans-serif;\n    font-size: 20px;\n\n    color: $MAIN_TEXT;\n}\n\n.card__subtitle {\n    grid-area: subtitle;\n    margin: 0;\n\n    font-family: 'Roboto', sans-serif;\n    font-size: 14px;\n    text-align: center;\n\n    color: $ADDITIONAL_TEXT;\n}\n\n.card__image {\n    grid-area: img;\n    align-self: center;\n    justify-self: center;\n    width: 50%;\n}\n\n.card__button {\n    grid-area: button;\n    display: flex;\n    justify-content: space-around;\n    align-items: center;\n    align-self: flex-end;\n    width: 100%;\n    height: 50%;\n    margin-left: auto;\n    margin-right: auto;\n    // height: 38px;\n\n    background-color: #00000000;\n    border: 3px solid $MAIN_COLOR;\n    border-radius: 0.5vw;\n\n    transition: all 0.3s ease;\n\n    & .card__price {\n        margin: 0;\n\n        font-family: 'Pulsar Original', sans-serif;\n        font-size: 14px;\n        color: $MAIN_TEXT;\n\n        & .card__price-old {\n            text-decoration: line-through;\n        }\n    }\n\n    & .card__cart-image {\n        width: 20%;\n        // height: 30px;\n    }\n}\n\n.card__rating {\n    position: relative;\n\n    grid-area: rating;\n    width: 50%;\n    justify-self: center;\n    align-self: center;\n\n    & .card__rating-image {\n        fill: $MAIN_COLOR;\n    }\n\n    & .card__rating-number {\n        position: absolute;\n        top: 30%;\n        left: 35%;\n\n        margin: 0;\n\n        font-family: 'Pulsar Original', sans-serif;\n        font-size: 20px;\n\n        color: $BACKGROUND;\n    }\n}\n\n.card__sale-image {\n    position: relative;\n\n    grid-area: sale;\n    width: 20px;\n    justify-self: center;\n    align-self: center;\n\n    fill: $MAIN_COLOR;\n}\n\n.card__info {\n    position: absolute;\n    bottom: -30px;\n    grid-area: info;\n    display: flex;\n    justify-content: space-around;\n    align-items: center;\n    margin: 0;\n    padding-left: 5%;\n    padding-right: 5%;\n\n    font-family: 'Robot', sans-serif;\n    font-size: 14px;\n\n    color: $MAIN_TEXT;\n    list-style: none;\n\n    transition: all 0.3s ease;\n}\n\n.card__info-stock,\n.card__info-players,\n.card__info-time {\n    display: flex;\n    align-items: center;\n\n    & svg {\n        width: 20%;\n        margin-right: 5%;\n    }\n}\n\n.card__info-age {\n    font-family: 'Pulsar Original', sans-serif;\n    font-size: 20px;\n}\n\n.scroll {\n    grid-area: scroll;\n    display: flex;\n    justify-content: center;\n    margin: 0;\n    padding: 0;\n\n    list-style: none;\n\n    & .scroll__item {\n        width: 20px;\n        height: 20px;\n        margin-left: 0.5vw;\n        margin-right: 0.5vw;\n\n        border: 3px solid $MAIN_COLOR;\n        border-radius: 20px;\n\n        &:hover {\n            scale: 1.1;\n            cursor: pointer;\n        }\n    }\n\n    & .scroll__item_active {\n        background-color: $MAIN_COLOR;\n    }\n}\n\n.sets-wrapper {\n    grid-area: sets;\n    // display: grid;\n    display: flex;\n    // grid-template-areas:\n    //     'category maker price price sale stock'\n    //     'category maker price price . .'\n    //     'category maker players players filter filter'\n  